import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, ViewController } from 'ionic-angular';
import { FirebaseProvider } from '../../providers/firebase';
import { LoadingProvider } from '../../providers/loading';

@IonicPage()
@Component({
  selector: 'page-questoes',
  templateUrl: 'questoes.html',
})
export class QuestoesPage {
  questoes: any;
  modal = false;
  
  constructor(
	  public navCtrl: NavController, 
	  public navParams: NavParams,
	  private firebaseProvider: FirebaseProvider,
      private loadingProvider: LoadingProvider,
      private modalCtrl: ModalController,
      private viewCtrl: ViewController
  ) {
	  this.getQuestoes();
      this.loadingProvider.present();
   	  this.modal = this.navParams.get('modal');
  }

//Refresh page
  refresh(refresher) {
    refresher.complete();
    this.navCtrl.setRoot(this.navCtrl.getActive().component);
  }

  //Close modal
  close() {
    this.viewCtrl.dismiss()
  }

  //Open assunto page
  open(p) {
    let modal = this.modalCtrl.create('AssuntosPage', { questoes: p });
    modal.present();
  }

  //Open cart
  openCart() {
    let modal = this.modalCtrl.create('CartPage', { modal: true });
    modal.present();
  }

  //List all assuntos to slides
  getQuestoes() {
    this.firebaseProvider.getQuestoes()
      .subscribe((res) => {
        this.loadingProvider.dismiss();
        this.questoes = res;
      })
  }
    //Open Details
  openDatailsQuest(value) {
	console.log(value);
	  
    let modal = this.modalCtrl.create('QuestDetailsPage', {data : value});
    modal.present();
  }

}



